import React from "react";
import styled from "styled-components";
import { Dropdown } from "./Rules/RulesStyle";

import { Chapter1 } from "./Rules/Chapter1";
import { Chapter2 } from "./Rules/Chapter2";
import { Chapter3 } from "./Rules/Chapter3";
import { Chapter4 } from "./Rules/Chapter4";
import { Chapter5 } from "./Rules/Chapter5";
import { Chapter6 } from "./Rules/Chapter6";
import { Chapter7 } from "./Rules/Chapter7";
import { Chapter8 } from "./Rules/Chapter8";
import { Chapter9 } from "./Rules/Chapter9";
import { Chapter10 } from "./Rules/Chapter10";
import { Chapter11 } from "./Rules/Chapter11";
import { Chapter12 } from "./Rules/Chapter12";
import { Chapter13 } from "./Rules/Chapter13";

const PageContainer = styled.div`
  margin-top: 50px;
  margin-bottom: 50px;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
`

const Chapter = styled.span `
  color: orange;
`

const Title = styled.span `
  font-size: 24px;
`

function RulesCNN() {
  return (
    <PageContainer>

      <Dropdown
        title={(<p><Title><Chapter> I. </Chapter> </Title><br/></p>)}
        content={( <Chapter1/> )}/>

      <Dropdown
        title={(<p><Title><Chapter> II. </Chapter>  .</Title><br/></p>)}
        content={( <Chapter2/> )}/>

      <Dropdown
        title={(<p><Title><Chapter> III. </Chapter>  .</Title><br/></p>)}
        content={( <Chapter3/> )}/>

      <Dropdown
        title={(<p><Title><Chapter> IV. </Chapter>  .</Title><br/></p>)}
        content={( <Chapter4/> )}/>

      <Dropdown
        title={(<p><Title><Chapter> V. </Chapter>   .</Title><br/></p>)}
        content={( <Chapter5/> )}/>

      <Dropdown
        title={(<p><Title><Chapter> VI. </Chapter> .</Title><br/></p>)}
        content={( <Chapter6/> )}/>

      <Dropdown
        title={(<p><Title><Chapter> VII. </Chapter> .</Title><br/></p>)}
        content={( <Chapter7/> )}/>

      <Dropdown
        title={(<p><Title><Chapter> VIIX. </Chapter>,   .</Title><br/></p>)}
        content={( <Chapter8/> )}/>

      <Dropdown
        title={(<p><Title><Chapter> IX. </Chapter> .</Title><br/></p>)}
        content={( <Chapter9/> )}/>

      <Dropdown
        title={(<p><Title><Chapter> X. </Chapter>   .</Title><br/></p>)}
        content={( <Chapter10/> )}/>

      <Dropdown
        title={(<p><Title><Chapter> XI. </Chapter>, , .</Title><br/></p>)}
        content={( <Chapter11/> )}/>

      <Dropdown
        title={(<p><Title><Chapter> XII. </Chapter>  .</Title><br/></p>)}
        content={( <Chapter12/> )}/>

      <Dropdown
        title={(<p><Title><Chapter> XIII. </Chapter>  .</Title><br/></p>)}
        content={( <Chapter13/> )}/>



    </PageContainer>
  );
}

export default RulesCNN;